import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Session } from 'protractor';


@Injectable({
  providedIn: 'root'
})
export class LoginService {

  private basePath = "http://localhost:8080";

  constructor(private client: HttpClient) { }

  public login(email: string, pass: string, type: string) {
    return this.client.post(this.basePath + "/login/" + email + "/" + pass + "/" + type,
      null, { responseType: 'text' });
  }

  public logout(): void {
    this.client.post(this.basePath + "/logout/" + sessionStorage.getItem("token"), null);

    sessionStorage.removeItem("token");
    alert("logged out successfully!")
  }



}
